import { Injectable } from '@angular/core';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/observable/forkJoin';
import * as firebase from 'firebase/app';
import { AngularFirestore } from 'angularfire2/firestore';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';
// import { e } from '@angular/core/src/render3';
// import { BehaviorSubject } from 'rxjs';
// import { map } from "rxjs/operators";
// import { Observable } from 'rxjs';
// import { forkJoin } from "rxjs//observable/forkJoin";
// import { combineLatest } from 'rxjs/observable/combineLatest';
// import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument, DocumentReference } from 'angularfire2/firestore';


export interface Task {
  id: string;
  title: string;
  description: string;
  location: string;
  date: string;
  person: string;
  reminder: string;
  frequency: string;
  status: string;
  shareWith: {displayName: string, email: string, uid: string}[];
  shareList: string[];
  userName: string;
};

/*
  Generated class for the FirestoreProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class FirestoreProvider {

  // referenceId;
  // public user$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  // public refUser$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  // sps$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  // cust$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  // coord$: BehaviorSubject<any> = new BehaviorSubject<any>([]);
  itrcode;

  constructor(  public firestore: AngularFirestore
              , public config: OtuConfigProvider
              ) {
    // console.log('Hello FirestoreProvider Provider');
    // this.user$.next(null);
    // this.refUser$.next(null);
    // this.sps$.next([]);
    // this.cust$.next([]);
    // this.coord$.next([]);
  }
  getTimeInFSFormat(d) {
    let date = new Date(d);
    date.setHours(date.getUTCHours());
    date.setMinutes(date.getUTCMinutes());
    // firebase.firestore.Timestamp.
    return firebase.firestore.Timestamp.fromDate(date);
  }
  getTimeFromSecs(m) {
    return firebase.firestore.Timestamp.fromMillis(m * 1000);
  }
  getItinerary(id) {
    // console.log(`Called with ${id} & ${this.config.getAppId()}`);
    
    return this.firestore.collection('itinerary', ref => ref.where("appId", "==", this.config.getAppId())
                                                            .where('id', "==", id));
  }
  // This is used
  eventLog(data) {
    let id = this.firestore.createId();
    data['id'] = id;
    data['appId'] = this.config.getAppId();
    this.firestore.doc(`apwa_events/${id}`).set(data);
  }
  setItrCode(itrcode) {
    if (this.itrcode != undefined && this.itrcode.itrcode != undefined) {
      alert('fs.ts - set Itr code ' + this.itrcode.itrcode);
    }
    this.itrcode = itrcode;
  }
  getItrCode() {
    if (this.itrcode != undefined && this.itrcode.itrcode != undefined) {
      alert('fs.ts - get Itr code ' + this.itrcode.itrcode);
      return this.itrcode.itrcode;
    } else {
      alert('fs.ts - get Itr code - null');
      return null;
    }
  }
  getOtherPackages() {
    return this.firestore.collection('packages', ref => ref.where("appId", "==", this.config.getAppId()));
  }
  getActivityMasterList() {
    return this.firestore.collection('activityMasterList', ref => ref.where("appId", "==", this.config.getAppId()));
  }
  rememberItrCode(token, itrCode) {
    // let id = this.firestore.createId();
    this.firestore.doc(`itrUser/${token}`).set({
      id: token,
      token: token,
      itrCode: itrCode,
      appId: this.config.getAppId()
    });
  }
  getLastUsedItrCode(token) {
    if(token == undefined || token == null) {
      token = '';
    }

    console.log(`token is ${token}`);
    
    return this.firestore.collection('itrUser', ref => ref.where("appId", "==", this.config.getAppId())
                                                          .where("token", "==", token));
  }
  getToken() {
    return this.firestore.createId();
  }
}



