import { FirestoreProvider } from './../../providers/firestore/firestore';
import { Component, Input } from '@angular/core';
import { OtuLoggerProvider } from '../../providers/otu-logger/otu-logger';
import { OtuConfigProvider } from '../../providers/otu-config/otu-config';

/**
 * Generated class for the ItineraryDayViewComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'itinerary-day-view',
  templateUrl: 'itinerary-day-view.html'
})
export class ItineraryDayViewComponent {

  // text: string;
  @Input('day') day;
  show: boolean = false;
  showDetails = [];
  lang = 'en';
  orderedKeys;
  activityTemplates;

constructor(
              private logger: OtuLoggerProvider
            , private config: OtuConfigProvider
            , private firestore: FirestoreProvider
            ) {
    // console.log('Hello ItineraryDayViewComponent Component');
    // this.text = 'Hello World';
    this.lang = 'en';
    this.config.getLanguage().subscribe((l) => this.lang = l);
    this.firestore.getActivityMasterList().valueChanges().subscribe((a) => {
      this.activityTemplates = a[0];
    })
  }

  ngOnInit() {
    // console.log(this.day);
    this.showDetails.fill(false, 0, this.day.length)
    // console.log(this.showDetails);
  }
  getObjectKeys(a) {
    // this.orderedKeys = Object.keys(a);
    // this.orderedKeys.forEach(x => {
    //   let activity = this.activityTemplates.fields.find(y => y.name == x);
    //   a[x]['displayOrder'] = activity.displayOrder;
    // })
    // a.sort(this.compare);
    // this.orderedKeys = Object.keys(a);
    // console.log(`The length is ${Object.keys(a).length}`);
    
    return Object.keys(a);
  }
  compare(a,b) {
    if (a.displayOrder < b.displayOrder)
      return -1;
    if (a.displayOrder > b.displayOrder)
      return 1;
    return 0;
  }
  getDescription(k){
    let activity = this.activityTemplates.fields.find((x) => 
    {
      return x.name == k;
    });
    if(activity == undefined) {
      return "NOT FOUND";
    } else {
      return activity.label[this.lang];
    }
  }
  getValue(k, val) {
    if(k == 'startTime' || k == 'endTime') {
      let s: string = '';
      let d = val.toDate();
      let h = d.getHours();
      if(h < 10) {
        s = '0';
      }
      s += h.toString() + ':';

      let m = d.getMinutes();
      if(m < 10) {
        s += '0';
      }
      s += m.toString();

      return s;

    } else if(typeof val == 'object') {
      if(k == 'contact') {
        let contact = [];
        if(val.name != undefined && val.name.trim() == '') {
          contact.push({description: 'Name', val: 'To be finalized soon...'});
        } else {
          contact.push({description: 'Name', val: val.name});
        }
        if(val.mob == '') {
          contact.push({description: 'mob', val: ' - '});
        } else {
          contact.push({description: 'mob', val: val.mob});
        }
        if(val.sms == '') {
          contact.push({description: 'sms', val: ' - '});
        } else {
          contact.push({description: 'sms', val: val.sms});
        }
        if(val.whatsapp == '') {
          contact.push({description: 'whatsapp', val: ' - '});
        } else {
          contact.push({description: 'whatsapp', val: val.whatsapp});
        }
        return contact;
      }
    } else if (val == '') {
      return 'SUDHIR: NULL'
    } else if(val == 0) {
      return 'ZERO'
    } else if(val == '0') {
      return 'SUDHIR: ZERO IN STRING'
    } else {
      return val;
    }
  }
  isMulitiple(k, val) {
    if(k == 'contact') {
        return true;
    }
    return false;
  }
  funTime(day, i) {
    this.logger.eventLog('funTime', {day: day, index: 1});
    alert('This is under construction. Stay tunned for more details');
  }
  toggleShow(day) {
    this.logger.eventLog('day click', {day: day});
    this.show = !this.show;
  }
  toggleShowDetails(day, activity, i, toggle) {
    this.logger.eventLog('activity click' + toggle, {day: day, activity: activity});
    this.showDetails[i] = !this.showDetails[i]
  }
}



