import { FirestoreProvider } from './../firestore/firestore';
import { Injectable } from '@angular/core';
import { Firebase } from '@ionic-native/firebase';
import { Platform } from 'ionic-angular';
import { Storage } from '@ionic/storage';

/*
  Generated class for the OtuLoggerProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class OtuLoggerProvider {

  events = ['Itinerary clicked'];
  device;
  location = {};
  token = '';

  constructor(
                private fba: Firebase
              , private firestore: FirestoreProvider
              , private platform: Platform
              , private storage: Storage
             ) {
    // console.log('Hello OtuLoggerProvider Provider');
    storage.get('alfrescoTestToken').then((t) => { 
      if(t == undefined || t == '') {
        this.token = firestore.getToken();
        alert('logger.ts  - First time token is ' + this.token);
        this.storage.set('alfrescoTestToken', this.token);
        } else {
        this.token = t;
        alert('logger.ts  - Repeat token is ' + this.token);
      }
    }, f => {
      this.token = firestore.getToken();
      this.storage.set('alfrescoTestToken', this.token);
      alert('logger.ts  - Failed token is ' + this.token);
    })
  }

  async eventLog(event, code) {
    // console.log("eventLog");
    if(this.platform.is('cordova')) {
      this.eventLogFB(event, code);
      this.eventLogFS(event, code);
      // console.log("On device");
    } else {
      // console.log("On Windows");
      
    }
  }
  async eventLogFS(event, code) {
    // Need to figure out a way by which this info can be provided without specific permission
    // if(this.device == undefined) {
      // this.device = this.unique.getDeviceId().then((d) => {
        this.device = {};
        this.saveEvent(event, code, 'Success');
        // console.log('eventLogFS is success'); 
      // }).catch(e => {
      //   console.log(e);     
      // })
    // } else {
    //   this.saveEvent(event, code, 'Success');
    // }
  }
  async eventLogFB(event, code) {
    code['timestamp'] = new Date();
    code['token'] = this.token;
    this.fba.logEvent(event, {
      code: code
    }).then((s) => {
      // console.log("Success");
      // console.log(s);
    }, (e) => {
      // console.log(e);
    })
  }

  async saveEvent(event, code, e) {
    this.firestore.eventLog({
      token: this.token,
      event: event,
      code: code,
      device: this.device,
      readStatus: e,
      location: this.location
      // ,
      // createdAt: new Date()
    })
  }
  setToken(token) {
    // this.token = token;
  }
  getToken() {
    alert('logger.ts  - Getting token ' + this.token);
    return this.token;
  }

}



