import { OtuLoggerProvider } from './../otu-logger/otu-logger';
import { Injectable } from '@angular/core';
import { Firebase } from '@ionic-native/firebase';
import { Platform } from 'ionic-angular';
import { AngularFirestore } from 'angularfire2/firestore';

/*
  Generated class for the AlertProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AlertProvider {

  constructor(
                private firebase: Firebase
              , private afs: AngularFirestore
              , private platform: Platform
              , private logger: OtuLoggerProvider
              ) {}

  async getToken() {
    let token;

    if (this.platform.is('android')) {
      token = await this.firebase.getToken();
    }

    if (this.platform.is('ios')) {
      token = await this.firebase.getToken();
      await this.firebase.grantPermission();
    }
    alert('alert.ts  - 1.token is ' + token);
    this.saveToken(token);
    
  }

  async saveToken(token) {
    let msg;
    if (!token) {
      msg = "Token Not Received";
      let interval = await setInterval(() => {
        token = this.logger.getToken();
        if(token != undefined && token != '') {
          clearInterval(interval);
          this.saveDevice(token, msg);
        }
      }, 100)
    } else {
      msg = "Token Received";
      this.saveDevice(token, msg);
    }
  }
  saveDevice(token, msg) {
    alert('alert.ts: 2.token is ' + token);
    alert('alert.ts: 2.msg is ' + msg);
    const devicesRef = this.afs.collection('devices');
    const data = {
      token,
      userId: 'userId',
      message: msg
    };
    // this.logger.setToken(token);
    this.logger.eventLog('getToken', data);
    alert('alert.ts  - 3.saving token');

    return devicesRef.doc(token).set(data);
  }
  onNotifications() {
    return this.firebase.onNotificationOpen();
  }
}


