import { Component } from '@angular/core';
import { Platform, ToastController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Location } from '@angular/common';

import { FirestoreProvider } from './../providers/firestore/firestore';
import { AlertProvider } from './../providers/alert/alert';
import { OtuLoggerProvider } from '../providers/otu-logger/otu-logger';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  rootPage:any = "UserItineraryPage";

  constructor(
                private platform: Platform
              // , statusBar: StatusBar
              // , splashScreen: SplashScreen
              , private firestore: FirestoreProvider
              , private location: Location
              , private alertProvider: AlertProvider
              , public toastController: ToastController
              , public logger: OtuLoggerProvider

              ) {
    alert('Component Constructor - V9');
    this.logger.eventLog('App Comp Const', {});
    this.initializeURLParams();
    this.notificationSetup();
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      // statusBar.styleDefault();
      // splashScreen.hide();
      // this.notificationSetup();
    });
  }
  initializeURLParams() {
    alert('Component.ts: initializeURLParams');
    const path = this.location.path(true),
      hasParams = /\?(.+?\=.+){1}/;
    let params;
  
    if (hasParams.test(path)) {
      params = {};
      path.split('?')[1].split('&').forEach(both => {
        let e = both.split('=');
        params[e[0]] = e[1];
      });
    }
    if(params == undefined || params == null) {
      this.logger.eventLog('initializeURLParams', {param: ''});
    } else {
      this.logger.eventLog('initializeURLParams', {param: params});
    }
    
    this.firestore.setItrCode(params);
  }
 
  private async presentToast(message) {
    this.logger.eventLog('received notification', {message: message});
    const toast = await this.toastController.create({
      message,
      duration: 3000
    });
    toast.present();
  }

  private notificationSetup() {
    alert('Component.ts: notificationSetup');
    this.alertProvider.getToken();
    this.alertProvider.onNotifications().subscribe(
      (msg) => {
        if (this.platform.is('ios')) {
            this.presentToast(msg.aps.alert);
        } else {
        this.presentToast(msg.body);
      }
    });
  }
}


